import Card from '../UI/Card';
import MealItem from './MealItem/MealItem';
import classes from './AvailableMeals.module.css';
import { useEffect, useState } from 'react';

const AvailableMeals = () => {
  const [meals, setMeals] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [httpError, setHttpError] = useState();
  useEffect(() => {
    const fetchMeals = async () => {
      const reponse = await fetch(
        'https://restikdb-default-rtdb.europe-west1.firebasedatabase.app/Meals.json'
      );

      if (!reponse.ok) {
        throw new Error('Something went wrong...');
      }
      const reponseData = await reponse.json();

      const loadedMeals = [];

      for (const key in reponseData) {
        loadedMeals.push({
          id: key,
          name: reponseData[key].name,
          description: reponseData[key].description,
          price: reponseData[key].price,
        });
      }
      setMeals(loadedMeals);
      setIsLoading(false);
    };
    fetchMeals().catch((error) => {
      setIsLoading(false);
      setHttpError(error.message);
    });
  }, []);

  if (isLoading) {
    return (
      <section className={classes.Mealsloading}>
        <p>Загрузка ...</p>
      </section>
    );
  }

  if (httpError) {
    return (
      <section className={classes.httpErrorMessage}>
        <p>{httpError}</p>
      </section>
    );
  }
  const mealsList = meals.map((meal) => (
    <MealItem
      key={meal.id}
      id={meal.id}
      name={meal.name}
      description={meal.description}
      price={meal.price}
    />
  ));

  return (
    <section className={classes.meals}>
      <Card>
        <ul>{mealsList}</ul>
      </Card>
    </section>
  );
};

export default AvailableMeals;
